import axios from 'axios'
import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { useParams } from 'react-router-dom'

export default function TvDetails() {

    let parameters = useParams();
    const [tvDetails, setTvDetails] = useState();

  async function showTvDetails(id){

    let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=f1aca93e54807386df3f6972a5c33b50`);
    setTvDetails(data);
    }
   
    useEffect(() => {
        showTvDetails(parameters.id)
    }, [])
    
  return (
    <>
{tvDetails? <div className="row text-white d-flex justify-content-center align-items-center my-5 py-5 vh-100">
    <div className="col-lg-3">
    <img className='w-100 mt-1' src={'https://image.tmdb.org/t/p/w500'+tvDetails.poster_path} alt="" />
    </div>
    <div className="col-lg-9">
        <h2 className='text-bold'>{tvDetails.name}</h2>
        <h5>Release Date : {tvDetails.release_date}</h5>
        <p>{tvDetails.overview}</p>
        <p>Vote : {tvDetails.vote_average}</p>
    </div>
</div> : <div className='d-flex justify-content-center align-items-center text-white vh-100'>
                    <i className='fas fa-spinner fa-spin fa-4x'> </i>
        </div>}
    </>
  )
}
